















import Vue from "vue";

export default Vue.extend({
  data: function () {
    return {
      password: "",
    };
  },
  computed: {
    wrongPassword() {
      return this.$store.state.accounts.wrongPassword;
    },
  },
  methods: {
    applyPassphrase() {
      this.$store.dispatch("accounts/applyPassphrase", this.password);
    },
  },
});
